export default {
	data() {
		return {}
	},
	onLoad(options) {
		console.log(options)
	},
	methods: {
		gotoRegistration: function() {
			uni.navigateTo({
				url: 'registration'
			});
		},
		gotoForgetPassword: function() {
			uni.navigateTo({
				url: 'forgetPasswd'
			});
		},
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value
			uni.request({
				url: "https://dongyi.sir6.cn/api/login/login",
				data: JSON.stringify(formdata),
				method:'POST',
				success: (res) => {
					console.log(res);
					if(res.data.err>0){
						uni.showToast({
							title:res.data.msg,
						})
					}else{
						console.log('登陆成功')
						var userinfo = res.data.data;
						uni.setStorageSync('USERID',userinfo.user_id);
						uni.setStorageSync('USERNAME',userinfo.name);
						uni.setStorageSync('MOBILE',userinfo.mobile);
						uni.setStorageSync('NICKNAME',userinfo.nick_name);
						uni.reLaunch({
							url: '/pages/index/index'
						})
					}

				}
			})
		},
	}
}
